"use client"

import { useState, useEffect, useRef } from "react"
import { ArrowLeft, RotateCcw } from "lucide-react"
import * as THREE from "three"
import { useMousePosition } from "@/hooks/use-mouse-position"
import { createEnhancedLights, createBoxMesh, createParticleSystem } from "@/lib/three-helpers"
import { UI_TEXT, GAME_CONFIG } from "@/lib/constants"

interface MissingNumberGameProps {
  onBack: () => void
}

interface Question {
  sequence: (number | null)[]
  missingIndex: number
  correct: number
  options: number[]
}

const generateQuestions = (): Question[] => {
  return [
    {
      sequence: [2, 4, null, 8],
      missingIndex: 2,
      correct: 6,
      options: [5, 6, 7, 8],
    },
    {
      sequence: [null, 6, 9, 12],
      missingIndex: 0,
      correct: 3,
      options: [1, 2, 3, 4],
    },
    {
      sequence: [1, null, 2, 3, 5],
      missingIndex: 1,
      correct: 1,
      options: [0, 1, 2, 3],
    },
    {
      sequence: [5, 10, null, 20],
      missingIndex: 2,
      correct: 15,
      options: [12, 13, 14, 15],
    },
    {
      sequence: [1, null, 4, 8],
      missingIndex: 1,
      correct: 2,
      options: [2, 3, 4, 5],
    },
  ]
}

export default function MissingNumberGame({ onBack }: MissingNumberGameProps) {
  const [questions] = useState<Question[]>(generateQuestions())
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [answered, setAnswered] = useState(false)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const canvasRef = useRef<HTMLDivElement>(null)
  const mouseRef = useMousePosition(canvasRef)

  useEffect(() => {
    if (!canvasRef.current) return

    const container = canvasRef.current
    const scene = new THREE.Scene()
    scene.fog = new THREE.Fog(
      GAME_CONFIG.THREE.BACKGROUND_COLOR,
      GAME_CONFIG.THREE.FOG_NEAR,
      GAME_CONFIG.THREE.FOG_FAR,
    )

    const camera = new THREE.PerspectiveCamera(
      GAME_CONFIG.THREE.FOV,
      container.clientWidth / container.clientHeight,
      GAME_CONFIG.THREE.NEAR,
      GAME_CONFIG.THREE.FAR,
    )
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(container.clientWidth, container.clientHeight)
    renderer.setClearColor(GAME_CONFIG.THREE.BACKGROUND_COLOR, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFSoftShadowMap
    container.appendChild(renderer.domElement)

    camera.position.z = 30

    const question = questions[currentQuestion]
    const groups: THREE.Group[] = []

    question.sequence.forEach((num, idx) => {
      const group = new THREE.Group()
      const isNull = num === null

      // Create enhanced cube with glow
      const color = isNull
        ? new THREE.Color(GAME_CONFIG.COLORS.LIGHT_PINK)
        : new THREE.Color().setHSL(Math.random(), 0.8, 0.6)
      
      const cube = createBoxMesh(1.5, color, {
        metalness: isNull ? 0.8 : 0.5,
        roughness: isNull ? 0.1 : 0.4,
        emissiveIntensity: isNull ? 0.9 : 0.4,
      })

      if (isNull) {
        cube.material.emissive = new THREE.Color(GAME_CONFIG.COLORS.LIGHT_PINK).multiplyScalar(0.8)
      }

      group.add(cube)

      // Enhanced edge glow for missing numbers with multiple layers
      if (isNull) {
        for (let i = 0; i < 3; i++) {
          const edgeGeom = new THREE.EdgesGeometry(new THREE.BoxGeometry(1.5 + i * 0.05, 1.5 + i * 0.05, 1.5 + i * 0.05))
          const lineMat = new THREE.LineBasicMaterial({
            color: GAME_CONFIG.COLORS.LIGHT_PINK,
            linewidth: 3 - i,
            transparent: true,
            opacity: (0.9 - i * 0.2),
          })
          const wireframe = new THREE.LineSegments(edgeGeom, lineMat)
          group.add(wireframe)
        }

        // Add pulsing glow spheres around missing cube
        const glowParticleCount = 8
        for (let i = 0; i < glowParticleCount; i++) {
          const glowGeom = new THREE.SphereGeometry(0.15, 16, 16)
          const glowMat = new THREE.MeshBasicMaterial({
            color: GAME_CONFIG.COLORS.LIGHT_PINK,
            transparent: true,
            opacity: 0.7,
            blending: THREE.AdditiveBlending,
          })
          const glowParticle = new THREE.Mesh(glowGeom, glowMat)
          const angle = (i / glowParticleCount) * Math.PI * 2
          glowParticle.userData = {
            angle: angle,
            distance: 2.2,
            speed: 0.05,
          }
          group.add(glowParticle)
        }
      } else {
        // Add subtle glow to regular cubes
        const glowGeom = new THREE.BoxGeometry(1.6, 1.6, 1.6)
        const glowMat = new THREE.MeshBasicMaterial({
          color: color.getHex(),
          transparent: true,
          opacity: 0.2,
          side: THREE.BackSide,
        })
        const glow = new THREE.Mesh(glowGeom, glowMat)
        group.add(glow)
      }

      const startX = -10 + idx * 5
      group.position.x = startX
      group.position.y = 0
      group.userData = {
        targetX: startX,
        number: num,
        index: idx,
        isNull,
      }

      scene.add(group)
      groups.push(group)
    })

    // Add background particle system
    const { particles: bgParticles, update: updateParticles } = createParticleSystem(120, 0xff006e, {
      size: 0.06,
      spread: 50,
      speed: 0.25,
    })
    scene.add(bgParticles)

    // Setup enhanced lighting
    createEnhancedLights(scene, { intensity: 3, distance: 200 })

    // Animation loop
    let animationId: number
    const animate = () => {
      animationId = requestAnimationFrame(animate)
      const time = Date.now() * 0.001

      // Update background particles
      updateParticles(time)

      groups.forEach((group) => {
        if (group.userData.isNull !== undefined) {
          // Enhanced rotation
          group.rotation.x += 0.004
          group.rotation.y += 0.004
          group.rotation.z += 0.002

          const timeOffset = time + group.userData.index * 0.5
          group.position.y = Math.sin(timeOffset) * 2.5
          group.position.z = Math.cos(timeOffset * 0.7) * 2.5

          // Enhanced mouse interaction for missing cube
          if (group.userData.isNull) {
            group.position.x += (mouseRef.current.x * 3 - group.position.x * 0.01) * 0.02
          }

          // Enhanced pulsing for missing cube
          if (group.userData.isNull) {
            const pulse = Math.sin(time * 3) * 0.3 + 1
            group.scale.set(pulse, pulse, pulse)

            // Animate glow particles
            group.children.forEach((child: THREE.Object3D) => {
              if (child instanceof THREE.Mesh && child.userData.angle !== undefined) {
                child.userData.angle += child.userData.speed
                const angle = child.userData.angle
                const distance = child.userData.distance
                
                child.position.x = Math.cos(angle) * distance
                child.position.y = Math.sin(angle * 0.8) * distance
                child.position.z = Math.sin(angle * 1.5) * distance * 0.6

                // Rotate and pulse
                child.rotation.x += 0.08
                child.rotation.y += 0.08
                const particlePulse = Math.sin(time * 4 + child.userData.angle) * 0.4 + 0.6
                child.scale.set(particlePulse, particlePulse, particlePulse)
              }
            })
          } else {
            // Subtle animation for regular cubes
            const subtlePulse = Math.sin(time * 1.5 + group.userData.index) * 0.05 + 1
            group.scale.set(subtlePulse, subtlePulse, subtlePulse)
          }
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      if (container) {
        camera.aspect = container.clientWidth / container.clientHeight
        camera.updateProjectionMatrix()
        renderer.setSize(container.clientWidth, container.clientHeight)
      }
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      cancelAnimationFrame(animationId)

      // Cleanup
      groups.forEach((group) => {
        group.traverse((object) => {
          if (object instanceof THREE.Mesh || object instanceof THREE.LineSegments) {
            object.geometry.dispose()
            if (Array.isArray(object.material)) {
              object.material.forEach((material) => material.dispose())
            } else {
              object.material.dispose()
            }
          }
        })
        scene.remove(group)
      })

      // Cleanup particles
      bgParticles.geometry.dispose()
      bgParticles.material.dispose()
      scene.remove(bgParticles)

      renderer.dispose()
      if (container.contains(renderer.domElement)) {
        container.removeChild(renderer.domElement)
      }
    }
  }, [currentQuestion, questions, mouseRef])

  const handleAnswer = (option: number) => {
    if (answered) return

    setSelectedAnswer(option)
    setAnswered(true)

    if (option === questions[currentQuestion].correct) {
      setScore((prev) => prev + 1)
    }
  }

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion((prev) => prev + 1)
      setAnswered(false)
      setSelectedAnswer(null)
    }
  }

  const handleRestart = () => {
    setCurrentQuestion(0)
    setScore(0)
    setAnswered(false)
    setSelectedAnswer(null)
  }

  const isGameComplete = currentQuestion === questions.length - 1 && answered
  const question = questions[currentQuestion]

  return (
    <div className="relative w-full h-full flex flex-col">
      <div ref={canvasRef} className="absolute inset-0" />

      <div className="absolute top-6 left-6 flex gap-4 z-10">
        <button
          onClick={onBack}
          className="flex items-center gap-2 px-4 py-2 bg-pink-600 hover:bg-pink-700 text-white rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl"
          aria-label={UI_TEXT.COMMON.BACK}
        >
          <ArrowLeft size={20} />
          {UI_TEXT.COMMON.BACK}
        </button>
        <button
          onClick={handleRestart}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl"
          aria-label={UI_TEXT.COMMON.RESTART}
        >
          <RotateCcw size={20} />
          {UI_TEXT.COMMON.RESTART}
        </button>
      </div>

      <div className="absolute top-6 right-6 z-10">
        <div className="text-2xl font-bold text-yellow-300 glow drop-shadow-lg">
          ‚≠ê {UI_TEXT.COMMON.SCORE}: {score}/{questions.length}
        </div>
      </div>

      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <div
          className="bg-slate-900/90 backdrop-blur-xl p-8 rounded-2xl max-w-2xl pointer-events-auto border-2 border-pink-500/40 shadow-2xl"
          style={{
            boxShadow: "0 0 50px rgba(236, 72, 153, 0.4), inset 0 0 30px rgba(236, 72, 153, 0.15)",
          }}
        >
          <div className="mb-8">
            <p className="text-sm text-pink-300 mb-3 font-semibold">
              C√¢u {currentQuestion + 1}/{questions.length}
            </p>
            <p className="text-xl font-bold text-white mb-6">{UI_TEXT.MISSING_NUMBER.TITLE}</p>

            <div className="flex gap-4 justify-center items-center mb-8 flex-wrap">
              {question.sequence.map((num, idx) => (
                <div key={`${num}-${idx}`} className="float-animation">
                  {num !== null ? (
                    <div className="w-16 h-16 bg-gradient-to-br from-pink-500 to-pink-700 rounded-lg flex items-center justify-center text-2xl font-bold text-white glow shadow-lg">
                      {num}
                    </div>
                  ) : (
                    <div className="w-16 h-16 bg-gradient-to-br from-gray-700 to-gray-900 rounded-lg flex items-center justify-center text-2xl font-bold text-pink-400 border-2 border-pink-400 border-dashed glow animate-pulse shadow-lg">
                      ?
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>

          {!answered ? (
            <div className="space-y-4">
              <p className="text-sm text-gray-300 mb-4">{UI_TEXT.MISSING_NUMBER.SELECT_ANSWER}</p>
              <div className="grid grid-cols-2 gap-4">
                {question.options.map((option) => (
                  <button
                    key={option}
                    onClick={() => handleAnswer(option)}
                    className="py-3 px-4 bg-gradient-to-r from-pink-600 to-rose-600 hover:from-pink-500 hover:to-rose-500 text-white font-bold rounded-lg transition-all duration-300 transform hover:scale-110 pulse-glow shadow-lg hover:shadow-xl"
                    aria-label={`Ch·ªçn ƒë√°p √°n ${option}`}
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>
          ) : (
            <div className="text-center">
              {selectedAnswer === question.correct ? (
                <div className="mb-6">
                  <div className="text-6xl mb-4 animate-bounce">üåü</div>
                  <p className="text-2xl font-bold text-green-400 mb-2">{UI_TEXT.MISSING_NUMBER.CORRECT}</p>
                  <p className="text-gray-300">{UI_TEXT.MISSING_NUMBER.CORRECT_DESC}</p>
                </div>
              ) : (
                <div className="mb-6">
                  <div className="text-6xl mb-4">ü§î</div>
                  <p className="text-2xl font-bold text-orange-400 mb-2">{UI_TEXT.MISSING_NUMBER.INCORRECT}</p>
                  <p className="text-gray-300">
                    {UI_TEXT.MISSING_NUMBER.CORRECT_ANSWER}{" "}
                    <span className="font-bold text-green-400">{question.correct}</span>
                  </p>
                </div>
              )}

              {!isGameComplete && (
                <button
                  onClick={handleNext}
                  className="mt-6 px-6 py-2 bg-green-600 hover:bg-green-700 text-white font-bold rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105"
                >
                  {UI_TEXT.COMMON.NEXT_QUESTION}
                </button>
              )}
            </div>
          )}

          {isGameComplete && (
            <div className="text-center mt-6">
              <p className="text-xl text-gray-300 mb-4">
                {UI_TEXT.MISSING_NUMBER.COMPLETE} {score} {UI_TEXT.MISSING_NUMBER.POINTS}
              </p>
              <button
                onClick={handleRestart}
                className="px-6 py-2 bg-pink-600 hover:bg-pink-700 text-white font-bold rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                {UI_TEXT.COMMON.PLAY_AGAIN}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
