"use client"

import { useEffect, useRef, useState } from "react"
import * as THREE from "three"
import { createGlowingSphere } from "@/lib/three-helpers"

const STAR_COUNT = 2000
const SUN_RADIUS = 12
const SUN_GLOW_RADIUS_1 = 13.5
const SUN_GLOW_RADIUS_2 = 15.5
const SUN_GLOW_RADIUS_3 = 18

const PLANETS = [
  {
    name: "Mercury",
    size: 3.5,
    distance: 28,
    speed: 0.08,
    color: 0x8c7853,
    emissive: 0x4a3d2a,
    rings: false,
    glowColor: 0xaa9066,
  },
  {
    name: "Venus",
    size: 4.5,
    distance: 42,
    speed: 0.03,
    color: 0xffc649,
    emissive: 0xffaa00,
    rings: false,
    glowColor: 0xffd880,
  },
  {
    name: "Earth",
    size: 4.2,
    distance: 58,
    speed: 0.018,
    color: 0x2e7cde,
    emissive: 0x1a4a8a,
    rings: false,
    glowColor: 0x4a9eff,
  },
  {
    name: "Mars",
    size: 3.8,
    distance: 75,
    speed: 0.012,
    color: 0xe27b58,
    emissive: 0x8a4a35,
    rings: false,
    glowColor: 0xff9d7a,
  },
] as const

export default function SolarSystemBackground() {
  const containerRef = useRef<HTMLDivElement>(null)
  const [hoveredPlanet, setHoveredPlanet] = useState<string | null>(null)
  const raycasterRef = useRef<THREE.Raycaster | null>(null)
  const mouseRef = useRef({ x: 0, y: 0 })

  useEffect(() => {
    if (!containerRef.current) return

    const container = containerRef.current
    const width = window.innerWidth
    const height = window.innerHeight

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 10000)
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    camera.position.z = 100
    renderer.setSize(width, height)
    renderer.setClearColor(0x0a0e27, 1)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFSoftShadowMap
    container.appendChild(renderer.domElement)

    // Enhanced stars background with twinkling
    const starsGeometry = new THREE.BufferGeometry()
    const starPositions = new Float32Array(STAR_COUNT * 3)
    const starSizes = new Float32Array(STAR_COUNT)
    const starOpacities = new Float32Array(STAR_COUNT)

    for (let i = 0; i < STAR_COUNT * 3; i += 3) {
      starPositions[i] = (Math.random() - 0.5) * 2000
      starPositions[i + 1] = (Math.random() - 0.5) * 2000
      starPositions[i + 2] = (Math.random() - 0.5) * 2000
      starSizes[i / 3] = Math.random() * 2 + 0.5
      starOpacities[i / 3] = Math.random()
    }

    starsGeometry.setAttribute("position", new THREE.BufferAttribute(starPositions, 3))
    starsGeometry.setAttribute("size", new THREE.BufferAttribute(starSizes, 1))
    starsGeometry.setAttribute("opacity", new THREE.BufferAttribute(starOpacities, 1))

    const starsMaterial = new THREE.PointsMaterial({
      color: 0xffffff,
      sizeAttenuation: true,
      transparent: true,
      opacity: 1,
      blending: THREE.AdditiveBlending,
    })
    const stars = new THREE.Points(starsGeometry, starsMaterial)
    scene.add(stars)

    // Enhanced Sun with multiple glow layers
    const sunGroup = new THREE.Group()

    // Main sun
    const sunGeometry = new THREE.SphereGeometry(SUN_RADIUS, 64, 64)
    const sunMaterial = new THREE.MeshStandardMaterial({
      color: 0xfdb813,
      emissive: 0xfdb813,
      emissiveIntensity: 1.2,
      metalness: 0.1,
      roughness: 0.3,
    })
    const sun = new THREE.Mesh(sunGeometry, sunMaterial)
    sun.castShadow = true
    sunGroup.add(sun)

    // Multiple glow layers
    for (let i = 0; i < 4; i++) {
      const glowRadius = SUN_RADIUS + 1.5 + i * 2
      const glowGeometry = new THREE.SphereGeometry(glowRadius, 32, 32)
      const glowMaterial = new THREE.MeshBasicMaterial({
        color: i === 0 ? 0xffa500 : i === 1 ? 0xff8c00 : i === 2 ? 0xff6b00 : 0xff4500,
        transparent: true,
        opacity: (0.4 - i * 0.08),
        side: THREE.BackSide,
      })
      const glow = new THREE.Mesh(glowGeometry, glowMaterial)
      sunGroup.add(glow)
    }

    scene.add(sunGroup)

    // Create enhanced planets with glow and rings
    const planetGroups: THREE.Group[] = []
    const planetMeshes: THREE.Mesh[] = []

    PLANETS.forEach((planet) => {
      const planetGroup = new THREE.Group()

      // Orbit line with glow
      const orbitGeometry = new THREE.BufferGeometry()
      const orbitPoints: number[] = []
      for (let i = 0; i <= 256; i++) {
        const angle = (i / 256) * Math.PI * 2
        orbitPoints.push(Math.cos(angle) * planet.distance, 0, Math.sin(angle) * planet.distance)
      }
      orbitGeometry.setAttribute("position", new THREE.BufferAttribute(new Float32Array(orbitPoints), 3))
      
      // Outer glow orbit
      const orbitGlowMaterial = new THREE.LineBasicMaterial({
        color: planet.glowColor,
        transparent: true,
        opacity: 0.1,
        linewidth: 2,
      })
      const orbitGlow = new THREE.Line(orbitGeometry, orbitGlowMaterial)
      scene.add(orbitGlow)

      // Main orbit line
      const orbitMaterial = new THREE.LineBasicMaterial({
        color: 0x4a5568,
        transparent: true,
        opacity: 0.5,
        linewidth: 1,
      })
      const orbit = new THREE.Line(orbitGeometry, orbitMaterial)
      scene.add(orbit)

      // Create glowing planet
      const planetColor = new THREE.Color(planet.color)
      const glowingPlanet = createGlowingSphere(planet.size, planetColor, {
        segments: 64,
        glowLayers: 4,
        glowIntensity: 0.7,
      })

      // Enhance planet material
      glowingPlanet.traverse((child: THREE.Object3D) => {
        if (child instanceof THREE.Mesh) {
          if (child.material instanceof THREE.MeshStandardMaterial) {
            child.material.emissive = new THREE.Color(planet.emissive)
            child.material.emissiveIntensity = 0.4
            child.material.metalness = 0.3
            child.material.roughness = 0.6
            child.castShadow = true
            child.receiveShadow = true
          } else if (child.material instanceof THREE.MeshBasicMaterial) {
            child.material.color = new THREE.Color(planet.glowColor)
          }
        }
      })

      planetGroup.add(glowingPlanet)

      // Add rings for larger planets (optional, can be enabled)
      if (planet.rings) {
        const ringGeometry = new THREE.RingGeometry(planet.size * 1.5, planet.size * 2.2, 64)
        const ringMaterial = new THREE.MeshBasicMaterial({
          color: planet.glowColor,
          transparent: true,
          opacity: 0.6,
          side: THREE.DoubleSide,
        })
        const ring = new THREE.Mesh(ringGeometry, ringMaterial)
        ring.rotation.x = Math.PI / 2
        planetGroup.add(ring)
      }

      // Add orbiting particles around planet
      const particleCount = 8
      for (let i = 0; i < particleCount; i++) {
        const particleGeom = new THREE.SphereGeometry(0.15, 16, 16)
        const particleMat = new THREE.MeshBasicMaterial({
          color: planet.glowColor,
          transparent: true,
          opacity: 0.6,
          blending: THREE.AdditiveBlending,
        })
        const particle = new THREE.Mesh(particleGeom, particleMat)
        const angle = (i / particleCount) * Math.PI * 2
        particle.userData = {
          angle: angle,
          distance: planet.size * 1.8,
          speed: 0.04,
        }
        planetGroup.add(particle)
      }

      // Store planet data
      planetGroup.userData = {
        angle: Math.random() * Math.PI * 2,
        ...planet,
        hovered: false,
      }

      scene.add(planetGroup)
      planetGroups.push(planetGroup)
      planetMeshes.push(glowingPlanet.children[0] as THREE.Mesh)
    })

    // Enhanced lighting
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5)
    scene.add(ambientLight)

    const sunLight = new THREE.PointLight(0xfdb813, 3, 300)
    sunLight.position.copy(sun.position)
    sunLight.castShadow = true
    scene.add(sunLight)

    // Directional light from sun
    const directionalLight = new THREE.DirectionalLight(0xfdb813, 1.5)
    directionalLight.position.set(0, 0, 0)
    directionalLight.castShadow = true
    scene.add(directionalLight)

    // Additional accent lights
    const accentLight1 = new THREE.PointLight(0x00d9ff, 1.5, 200)
    accentLight1.position.set(-120, 60, 60)
    scene.add(accentLight1)

    const accentLight2 = new THREE.PointLight(0xff006e, 1.2, 200)
    accentLight2.position.set(120, -60, 60)
    scene.add(accentLight2)

    // Raycaster for interaction
    const raycaster = new THREE.Raycaster()
    raycasterRef.current = raycaster

    // Mouse tracking
    const handleMouseMove = (e: MouseEvent) => {
      mouseRef.current.x = (e.clientX / width) * 2 - 1
      mouseRef.current.y = -(e.clientY / height) * 2 + 1
    }

    const handleClick = (e: MouseEvent) => {
      const mouse = new THREE.Vector2()
      mouse.x = (e.clientX / width) * 2 - 1
      mouse.y = -(e.clientY / height) * 2 + 1

      raycaster.setFromCamera(mouse, camera)
      const intersects = raycaster.intersectObjects(planetMeshes, true)

      if (intersects.length > 0) {
        const planetMesh = intersects[0].object
        const planetGroup = planetMesh.parent as THREE.Group
        if (planetGroup && planetGroup.userData.name) {
          console.log(`Clicked on ${planetGroup.userData.name}`)
          // You can add more interaction here
        }
      }
    }

    window.addEventListener("mousemove", handleMouseMove)
    window.addEventListener("click", handleClick)

    const handleResize = () => {
      const newWidth = window.innerWidth
      const newHeight = window.innerHeight
      camera.aspect = newWidth / newHeight
      camera.updateProjectionMatrix()
      renderer.setSize(newWidth, newHeight)
    }

    window.addEventListener("resize", handleResize)

    // Animation loop
    let animationId: number
    const animate = () => {
      animationId = requestAnimationFrame(animate)

      const time = Date.now() * 0.001

      // Rotate sun with enhanced effects
      sunGroup.rotation.y += 0.004
      sunGroup.children.forEach((child, i) => {
        if (i > 0) {
          child.rotation.y += (0.002 - i * 0.0005) * (i % 2 === 0 ? 1 : -1)
        }
      })

      // Pulsing sun glow
      const pulseScale = 1 + Math.sin(time * 1.5) * 0.15
      sunGroup.children[1].scale.set(pulseScale, pulseScale, pulseScale)

      // Animate planets with enhanced effects
      planetGroups.forEach((planetGroup) => {
        const planetData = planetGroup.userData

        // Orbital motion
        planetData.angle += planetData.speed
        planetGroup.position.x = Math.cos(planetData.angle) * planetData.distance
        planetGroup.position.z = Math.sin(planetData.angle) * planetData.distance

        // Enhanced rotation
        planetGroup.rotation.y += 0.02
        planetGroup.rotation.x = Math.sin(planetData.angle * 0.3) * 0.1

        // Enhanced y wobble
        planetGroup.position.y = Math.sin(planetData.angle * 0.5) * 3

        // Scale pulsing
        const scale = 1 + Math.sin(time * 0.8 + planetData.angle) * 0.1
        planetGroup.scale.set(scale, scale, scale)

        // Animate orbiting particles
        planetGroup.children.forEach((child: THREE.Object3D) => {
          if (child instanceof THREE.Mesh && child.userData.angle !== undefined) {
            child.userData.angle += child.userData.speed
            const angle = child.userData.angle
            const distance = child.userData.distance

            child.position.x = Math.cos(angle) * distance
            child.position.y = Math.sin(angle * 0.7) * distance
            child.position.z = Math.sin(angle * 1.2) * distance * 0.5

            child.rotation.x += 0.05
            child.rotation.y += 0.05

            // Pulsing particles
            const particlePulse = Math.sin(time * 3 + child.userData.angle) * 0.4 + 0.6
            child.scale.set(particlePulse, particlePulse, particlePulse)
          }
        })

        // Hover effect detection
        raycaster.setFromCamera(new THREE.Vector2(mouseRef.current.x, mouseRef.current.y), camera)
        const intersects = raycaster.intersectObject(planetMeshes[planetGroups.indexOf(planetGroup)], true)
        
        if (intersects.length > 0 && !planetData.hovered) {
          planetData.hovered = true
          setHoveredPlanet(planetData.name)
        } else if (intersects.length === 0 && planetData.hovered) {
          planetData.hovered = false
          if (hoveredPlanet === planetData.name) {
            setHoveredPlanet(null)
          }
        }

        // Hover scale effect
        if (planetData.hovered) {
          const hoverScale = 1.15
          planetGroup.scale.lerp(new THREE.Vector3(hoverScale, hoverScale, hoverScale), 0.1)
        }
      })

      // Enhanced camera movement based on mouse
      camera.position.x += (mouseRef.current.x * 20 - camera.position.x) * 0.03
      camera.position.y += (mouseRef.current.y * 20 - camera.position.y) * 0.03
      camera.lookAt(scene.position)

      // Twinkling stars
      const opacities = starsGeometry.attributes.opacity.array as Float32Array
      for (let i = 0; i < STAR_COUNT; i++) {
        opacities[i] = Math.sin(time * 2 + i) * 0.5 + 0.5
      }
      starsGeometry.attributes.opacity.needsUpdate = true

      // Rotate stars slowly
      stars.rotation.y += 0.00003

      renderer.render(scene, camera)
    }

    animate()

    return () => {
      window.removeEventListener("resize", handleResize)
      window.removeEventListener("mousemove", handleMouseMove)
      window.removeEventListener("click", handleClick)
      cancelAnimationFrame(animationId)

      // Cleanup
      scene.traverse((object: THREE.Object3D) => {
        if (object instanceof THREE.Mesh || object instanceof THREE.Line || object instanceof THREE.Points) {
          if (object.geometry) object.geometry.dispose()
          if (object.material) {
            if (Array.isArray(object.material)) {
              object.material.forEach((material: THREE.Material) => material.dispose())
            } else {
              object.material.dispose()
            }
          }
        }
      })

      renderer.dispose()
      if (container.contains(renderer.domElement)) {
        container.removeChild(renderer.domElement)
      }
    }
  }, [hoveredPlanet])

  return (
    <>
      <div ref={containerRef} className="fixed inset-0 -z-10" />
      {hoveredPlanet && (
        <div className="fixed top-20 left-1/2 transform -translate-x-1/2 z-50 pointer-events-none">
          <div className="bg-slate-900/90 backdrop-blur-md px-6 py-3 rounded-lg border-2 border-blue-500/50 shadow-xl">
            <p className="text-white font-bold text-lg">{hoveredPlanet}</p>
          </div>
        </div>
      )}
    </>
  )
}
