"use client"

import { useState, useEffect, useRef } from "react"
import { ArrowLeft, RotateCcw } from "lucide-react"
import * as THREE from "three"
import { createEnhancedLights, createGlowingSphere, createParticleSystem } from "@/lib/three-helpers"
import { UI_TEXT, GAME_CONFIG } from "@/lib/constants"

interface SortSequenceGameProps {
  onBack: () => void
}

interface Question {
  numbers: number[]
  correct: number[]
  description: string
}

const generateQuestions = (): Question[] => {
  return [
    {
      numbers: [8, 2, 6, 4],
      correct: [2, 4, 6, 8],
      description: "S·∫Øp x·∫øp theo th·ª© t·ª± tƒÉng d·∫ßn (C·ªông 2)",
    },
    {
      numbers: [12, 3, 9, 6],
      correct: [3, 6, 9, 12],
      description: "S·∫Øp x·∫øp theo th·ª© t·ª± tƒÉng d·∫ßn (C·ªông 3)",
    },
    {
      numbers: [3, 5, 1, 2],
      correct: [1, 2, 3, 5],
      description: "S·∫Øp x·∫øp theo th·ª© t·ª± tƒÉng d·∫ßn",
    },
    {
      numbers: [16, 4, 8, 2],
      correct: [2, 4, 8, 16],
      description: "S·∫Øp x·∫øp theo th·ª© t·ª± tƒÉng d·∫ßn (Nh√¢n 2)",
    },
    {
      numbers: [25, 5, 15, 20],
      correct: [5, 15, 20, 25],
      description: "S·∫Øp x·∫øp theo th·ª© t·ª± tƒÉng d·∫ßn (C·ªông 5)",
    },
  ]
}

export default function SortSequenceGame({ onBack }: SortSequenceGameProps) {
  const [questions] = useState<Question[]>(generateQuestions())
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [answered, setAnswered] = useState(false)
  const [selected, setSelected] = useState<number[]>([])
  const canvasRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!canvasRef.current) return

    const container = canvasRef.current
    const scene = new THREE.Scene()
    scene.fog = new THREE.Fog(
      GAME_CONFIG.THREE.BACKGROUND_COLOR,
      GAME_CONFIG.THREE.FOG_NEAR + 20,
      GAME_CONFIG.THREE.FOG_FAR,
    )

    const camera = new THREE.PerspectiveCamera(
      GAME_CONFIG.THREE.FOV,
      container.clientWidth / container.clientHeight,
      GAME_CONFIG.THREE.NEAR,
      GAME_CONFIG.THREE.FAR,
    )
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(container.clientWidth, container.clientHeight)
    renderer.setClearColor(GAME_CONFIG.THREE.BACKGROUND_COLOR, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFSoftShadowMap
    container.appendChild(renderer.domElement)

    camera.position.z = 35

    // Create enhanced particle system
    const { particles: bgParticles, update: updateParticles } = createParticleSystem(200, 0xffffff, {
      size: 0.08,
      spread: 50,
      speed: 0.4,
    })
    scene.add(bgParticles)

    // Create larger glowing particles
    const largeParticleCount = 30
    const largeParticles: THREE.Mesh[] = []

    for (let i = 0; i < largeParticleCount; i++) {
      const hue = Math.random()
      const color = new THREE.Color().setHSL(hue, 0.95, 0.7)
      const glowingParticle = createGlowingSphere(0.4, color, {
        segments: 32,
        glowLayers: 3,
        glowIntensity: 0.8,
      })

      glowingParticle.position.x = (Math.random() - 0.5) * 40
      glowingParticle.position.y = (Math.random() - 0.5) * 40
      glowingParticle.position.z = (Math.random() - 0.5) * 30

      glowingParticle.userData = {
        vx: (Math.random() - 0.5) * 0.02,
        vy: (Math.random() - 0.5) * 0.02,
        vz: (Math.random() - 0.5) * 0.02,
        hue: hue,
        index: i,
      }

      scene.add(glowingParticle)
      largeParticles.push(glowingParticle.children[0] as THREE.Mesh)
    }

    // Setup enhanced lighting
    createEnhancedLights(scene, { intensity: 3.5, distance: 200 })

    // Additional colored lights for this game
    const accentLight1 = new THREE.PointLight(0xb537f2, 2, 120)
    accentLight1.position.set(25, 25, 25)
    scene.add(accentLight1)

    const accentLight2 = new THREE.PointLight(0x39ff14, 2, 120)
    accentLight2.position.set(-25, -25, 25)
    scene.add(accentLight2)

    // Animation loop
    let animationId: number
    const animate = () => {
      animationId = requestAnimationFrame(animate)

      const time = Date.now() * 0.001

      // Update background particles
      updateParticles(time)

      // Animate large glowing particles
      largeParticles.forEach((particle) => {
        const parent = particle.parent as THREE.Group
        if (!parent) return

        // Movement
        parent.position.x += parent.userData.vx
        parent.position.y += parent.userData.vy
        parent.position.z += parent.userData.vz

        // Bounce off boundaries
        if (Math.abs(parent.position.x) > 25) parent.userData.vx *= -1
        if (Math.abs(parent.position.y) > 25) parent.userData.vy *= -1
        if (Math.abs(parent.position.z) > 20) parent.userData.vz *= -1

        // Rotation
        parent.rotation.x += 0.015
        parent.rotation.y += 0.015
        parent.rotation.z += 0.01

        // Color cycling effect
        const hue = (time * 0.15 + parent.userData.index * 0.02) % 1
        const color = new THREE.Color().setHSL(hue, 0.95, 0.7)
        
        parent.traverse((child: THREE.Object3D) => {
          if (child instanceof THREE.Mesh) {
            if (child.material instanceof THREE.MeshStandardMaterial) {
              child.material.color.copy(color)
              child.material.emissive.copy(color).multiplyScalar(0.8)
            } else if (child.material instanceof THREE.MeshBasicMaterial) {
              child.material.color.copy(color)
            }
          }
        })

        // Enhanced scale pulsing
        const scale = Math.sin(time * 2.5 + parent.userData.index * 0.15) * 0.4 + 1
        parent.scale.set(scale, scale, scale)
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      if (container) {
        camera.aspect = container.clientWidth / container.clientHeight
        camera.updateProjectionMatrix()
        renderer.setSize(container.clientWidth, container.clientHeight)
      }
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      cancelAnimationFrame(animationId)

      // Cleanup large particles
      largeParticles.forEach((particle) => {
        const parent = particle.parent as THREE.Group
        if (parent) {
          parent.traverse((child: THREE.Object3D) => {
            if (child instanceof THREE.Mesh) {
              child.geometry.dispose()
              if (Array.isArray(child.material)) {
                child.material.forEach((material: THREE.Material) => material.dispose())
              } else {
                child.material.dispose()
              }
            }
          })
          scene.remove(parent)
        }
      })

      // Cleanup background particles
      bgParticles.geometry.dispose()
      bgParticles.material.dispose()
      scene.remove(bgParticles)

      renderer.dispose()
      if (container.contains(renderer.domElement)) {
        container.removeChild(renderer.domElement)
      }
    }
  }, [currentQuestion])

  const handleSelectNumber = (num: number) => {
    if (answered) return

    const newSelected = [...selected]
    const existingIndex = newSelected.indexOf(num)

    if (existingIndex > -1) {
      newSelected.splice(existingIndex, 1)
    } else {
      newSelected.push(num)
    }

    setSelected(newSelected)

    // Check if all numbers are selected
    if (newSelected.length === questions[currentQuestion].numbers.length) {
      const isCorrect = JSON.stringify(newSelected) === JSON.stringify(questions[currentQuestion].correct)
      setAnswered(true)

      if (isCorrect) {
        setScore((prev) => prev + 1)
      }
    }
  }

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion((prev) => prev + 1)
      setAnswered(false)
      setSelected([])
    }
  }

  const handleRestart = () => {
    setCurrentQuestion(0)
    setScore(0)
    setAnswered(false)
    setSelected([])
  }

  const isGameComplete = currentQuestion === questions.length - 1 && answered
  const question = questions[currentQuestion]
  const unselected = question.numbers.filter((n) => !selected.includes(n))

  return (
    <div className="relative w-full h-full flex flex-col">
      <div ref={canvasRef} className="absolute inset-0" />

      <div className="absolute top-6 left-6 flex gap-4 z-10">
        <button
          onClick={onBack}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300"
          aria-label={UI_TEXT.COMMON.BACK}
        >
          <ArrowLeft size={20} />
          {UI_TEXT.COMMON.BACK}
        </button>
        <button
          onClick={handleRestart}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300"
          aria-label={UI_TEXT.COMMON.RESTART}
        >
          <RotateCcw size={20} />
          {UI_TEXT.COMMON.RESTART}
        </button>
      </div>

      <div className="absolute top-6 right-6 z-10">
        <div className="text-2xl font-bold text-yellow-300 glow">
          ‚≠ê {UI_TEXT.COMMON.SCORE}: {score}/{questions.length}
        </div>
      </div>

      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <div
          className="bg-slate-900/80 backdrop-blur-md p-8 rounded-2xl max-w-3xl pointer-events-auto border-2 border-purple-500/30"
          style={{
            boxShadow: "0 0 30px rgba(181, 55, 242, 0.3), inset 0 0 20px rgba(181, 55, 242, 0.1)",
          }}
        >
          <div className="mb-8">
            <p className="text-sm text-purple-300 mb-3">
              C√¢u {currentQuestion + 1}/{questions.length}
            </p>
            <p className="text-xl font-bold text-white mb-2">{question.description}</p>
            <p className="text-sm text-gray-300 mb-6">{UI_TEXT.SORT_SEQUENCE.INSTRUCTIONS}</p>

            <div className="bg-gradient-to-r from-purple-900/50 to-pink-900/50 p-6 rounded-lg mb-6 border border-purple-500/30">
              <p className="text-sm text-gray-300 mb-3">{UI_TEXT.SORT_SEQUENCE.SELECT_NUMBERS}</p>
              <div className="flex gap-3 flex-wrap justify-center">
                {unselected.map((num) => (
                  <button
                    key={num}
                    onClick={() => handleSelectNumber(num)}
                    disabled={answered}
                    className="w-14 h-14 bg-gradient-to-br from-purple-500 to-pink-500 hover:from-purple-400 hover:to-pink-400 disabled:opacity-50 text-white font-bold rounded-lg transition-all duration-300 transform hover:scale-110 pulse-glow"
                    aria-label={`Ch·ªçn s·ªë ${num}`}
                  >
                    {num}
                  </button>
                ))}
              </div>
            </div>

            <div className="bg-gradient-to-r from-green-900/50 to-emerald-900/50 p-6 rounded-lg border border-green-500/30">
              <p className="text-sm text-gray-300 mb-3">{UI_TEXT.SORT_SEQUENCE.SORTED_SEQUENCE}</p>
              <div className="flex gap-3 flex-wrap justify-center min-h-16 items-center">
                {selected.length > 0 ? (
                  selected.map((num, idx) => (
                    <div key={`${num}-${idx}`} className="float-animation">
                      <div className="w-14 h-14 bg-gradient-to-br from-green-500 to-emerald-500 text-white font-bold rounded-lg flex items-center justify-center text-lg glow">
                        {num}
                      </div>
                    </div>
                  ))
                ) : (
                  <p className="text-gray-400">{UI_TEXT.SORT_SEQUENCE.SELECT_NUMBERS_PLACEHOLDER}</p>
                )}
              </div>
            </div>
          </div>

          {answered && (
            <div className="text-center">
              {JSON.stringify(selected) === JSON.stringify(question.correct) ? (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üöÄ</div>
                  <p className="text-2xl font-bold text-green-400 mb-2">{UI_TEXT.SORT_SEQUENCE.CORRECT}</p>
                  <p className="text-gray-300">{UI_TEXT.SORT_SEQUENCE.CORRECT_DESC}</p>
                </div>
              ) : (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üí™</div>
                  <p className="text-2xl font-bold text-orange-400 mb-2">{UI_TEXT.SORT_SEQUENCE.INCORRECT}</p>
                  <p className="text-gray-300">
                    {UI_TEXT.SORT_SEQUENCE.CORRECT_ANSWER}{" "}
                    <span className="font-bold text-green-400">{question.correct.join(", ")}</span>
                  </p>
                </div>
              )}

              {!isGameComplete && (
                <button
                  onClick={handleNext}
                  className="mt-6 px-6 py-2 bg-green-600 hover:bg-green-700 text-white font-bold rounded-lg transition-all duration-300"
                >
                  {UI_TEXT.COMMON.NEXT_QUESTION}
                </button>
              )}
            </div>
          )}

          {isGameComplete && (
            <div className="text-center mt-6">
              <p className="text-xl text-gray-300 mb-4">
                {UI_TEXT.SORT_SEQUENCE.COMPLETE} {score} {UI_TEXT.SORT_SEQUENCE.POINTS}
              </p>
              <button
                onClick={handleRestart}
                className="px-6 py-2 bg-purple-600 hover:bg-purple-700 text-white font-bold rounded-lg transition-all duration-300"
              >
                {UI_TEXT.COMMON.PLAY_AGAIN}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
